@charset "utf-8";

.product-detail-page {
    background-color: var(--light-background);
    border-radius: 12px;
}

.product-main-detail {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    align-items: flex-start;
    justify-content: center;
    margin-bottom: 3rem;
}

.product-main-detail img {
    width: 320px;
    max-height: 420px;
    object-fit: contain;
    border-radius: 8px;
    padding: 1rem;
}

.product-main-detail > div {
    flex: 1;
}

.product-main-detail h3 {
    font-size: 1.5rem;
    color: var(--app-brown);
    margin-bottom: 1rem;
}

.product-main-detail p {
    margin-bottom: 0.8rem;
    line-height: 1.6;
    color: var(--text-dark);
}

.product-main-detail strong {
    font-weight: 600;
}

.star-icon {
    color: var(--highlight);
    margin-left: 4px;
}

.suggested-products {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-top: 2rem;
}

.product-box {
    border: 2px solid var(--app-light-gray); 
    width: 450px;
    border-radius: 10px;
    padding: 1rem;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}   

.product-box:hover {
    transform: scale(1.01);
    box-shadow: 0 4px 12px rgb(0 0 0 / 0.08);
}

.product-img-box {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.product-img-box img {
    width: 200px;
    height: 200px;
    object-fit: contain;
}

.product-detail {
    padding: 0.5rem 0;
    color: var(--app-light-gray);
    font-weight: 600;
    font-size: 14px;
}

.product-detail p:nth-of-type(2) {
    color: red;
}

.suggested-products .product-box {
    width: 240px;
}

.suggested-products .product-detail {
    padding-top: 15px;
}

.product-box p:nth-of-type(1) {
    height: 40px;
}

@media (max-width: 768px) {
  .product-main-detail {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .product-main-detail img {
    width: 80%;
  }
} 

